import Vue from 'vue'
import VueRouter from 'vue-router'


Vue.use(VueRouter)

import About from "../views/About.vue"
import Index from "../views/Index.vue"
import Login from "../views/Login.vue"
import Register from "../views/Reg.vue"
import Contact from "../views/Contact.vue"
import News from "../views/News.vue"
import Details from "../views/Details.vue"
import Product from "../views/Product.vue"
import ProductDetails from "../views/ProductDetails.vue"
import Cart from "../views/Cart.vue"


const routes = [
  {path:"/",name:"Index",component:Index,},
  {path:"/about",name:"About",component:About,},
  {path:"/login",name:"Login",component:Login,},
  {path:"/register",name:"Register",component:Register,},
  {path:"/contact",name:"Contact",component:Contact,},
  {path:"/news",name:"News",component:News,},
  {path:"/details/:nid",name:"Details",component:Details,props:true},
  {path:"/product/:type",name:"Product",component:Product,props:true},
  {path:"/productdetails/:pid",name:"ProductDetails",component:ProductDetails,props:true},
  {path:"/cart",name:"Cart",component:Cart,props:true},
  // {
  //   path: '/about',
  //   name: 'About',
  //   // route level code-splitting
  //   // this generates a separate chunk (about.[hash].js) for this route
  //   // which is lazy-loaded when the route is visited.
  //   component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
  // },
]

const router = new VueRouter({
  mode: 'history',
  // base: process.env.BASE_URL,
  routes
})

export default router
