<script>
export default {
  data() {
    return {
      cur: 1 // 表示当前显示第几张图片
    };
  },
  methods: {
    next() {
      this.cur++;
      if (this.cur == 5) this.cur = 1;
    },
    prev() {
      this.cur--;
      if (this.cur == 0) this.cur = 4;
    }
  },
  mounted() {
    this.timer = setInterval(() => {
      this.next();
    }, 3000);
  },
  // 组件销毁时关闭定时器
  beforeDestroy() {
    clearInterval(this.timer);
  }
};
</script>

