<template>
  <div>
    <div class="breadcrumb">
      <div class="container">
        <h2>
          <router-link to="/">首页</router-link>&gt;
          <a href>产品中心</a>&gt;
          <a href>净美仕净化器</a>
        </h2>
      </div>
    </div>
    <!--页面主体-->
    <div class="main container" v-if="pro">
      <div class="pd_info clearfloat">
        <div class="pdinfo_img">
          <img :src="'http://101.96.128.94:9999/mfresh/'+pro.pic" />
        </div>
        <div class="pdinfo_text">
          <h2>{{pro.title}}</h2>
          <ul>
            <li>型号：{{pro.model}}</li>
            <li>功能：{{pro.func}}</li>
            <li>噪音：{{pro.noise}}</li>
            <li>风量：{{pro.wind}}</li>
            <li>适用对象：{{pro.applyTo}}</li>
            <li>适用面积：{{pro.size}}</li>
            <li>空气净化能效等级：{{pro.level}}</li>
          </ul>
          <p>
            价格：
            <span>¥</span>
            <strong id="price">{{pro.price}}</strong>
          </p>
          <a @click="addCart(pro.pid)" id="addCart">
            <span class="icon_cart"></span>加入购物车
          </a>
        </div>
      </div>
      <div class="details_box" v-if="pro">
        <h2 class="title">产品详情</h2>
        <div class="pd_details" v-html="pro.detail">
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props:["pid"],
  data() {
    return {
      pro:null
    }
  },
  mounted(){
    let url="product_detail.php?pid="+this.pid
    this.axios.get(url)
    .then(res => {
      this.pro=res.data;
      this.pro.detail=this.pro.detail.replace(/img src="/g,'img src="http://101.96.128.94:9999/mfresh/')
    })
    .catch(err => {
      console.error(err); 
    })
  },
  methods:{
    addCart(pid){
      if(!this.$store.state.uid){
        alert("请先登录")
        this.$router.push("/login")
      }else{
        let url="cart_detail_add.php";
        let params="uid="+this.$store.state.uid+"&pid="+pid
        this.axios.post(url,params)
        .then(res => {
          if(res.data.code){
            alert("添加成功")
          }else{
            alert("sth wrong")
          }
        })
        .catch(err => {
          console.error(err); 
        })
      }
    }
  }
};
</script>

