<template>
  <div>
    <div class="breadcrumb">
      <div class="container">
        <h2>
          <router-link to="/">首页</router-link>&gt;
          <router-link to="/news">公司动态</router-link>
        </h2>
      </div>
    </div>
    <!--页面主体-->
    <div class="main container" v-show="!!detail">
      <div class="news_details">
        <h2>{{title}}</h2>
        <span>发布时间：{{pubTime|date}}</span>
        <div class="news_content" v-html="detail">
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: ["nid"],
  data() {
    return {
      detail: "",
      title: "",
      pubTime: 0
    };
  },
  mounted() {
    // console.log(this.nid)
    let url = "news_detail.php?nid="+this.nid;
    this.axios
      .get(url)
      .then(res => {
        console.log(res);
        this.detail = res.data.content;
        this.detail = this.detail.replace(
          /src="/g,
          'src="http://101.96.128.94:9999/mfresh/'
        );
        this.title = res.data.title;
        this.pubTime = res.data.pubTime;
      })
      .catch(err => {
        console.error(err);
      });
  }
};
</script>

