<template>
  <div>
    <div class="breadcrumb">
      <div class="container">
        <h2>
          <router-link to="/">首页</router-link>&gt;
          <a href>产品中心</a>
        </h2>
      </div>
    </div>
    <!--页面主体-->
    <div class="main container">
      <div class="cart">
        <div class="cart_header">
          <span>
            已选商品
            <em>{{count}}</em>件
          </span>
          <span>
            总金额：
            <strong>￥{{total}}</strong>
          </span>
          <button type="button">结算</button>
        </div>
        <div class="cart_con">
          <div class="cartcon_title">
            <span>
              <input type="checkbox" v-model="allin"/> 全选
            </span>
            <span>商品信息</span>
            <span>单价</span>
            <span>数量</span>
            <span>金额</span>
            <span>操作</span>
          </div>
          <ul>
            <h2 v-if="pro.length==0">购物车中没有任何商品！</h2>
            <li v-else v-for="(item, index) in pro" :key="index">
              <!-- input标签都可以添加v-model属性 -->
              <input type="checkbox" class="cart_checkbox" v-model="item.checked" @change="docheck()"/>
              <a class="cart_img">
                <img :src="'http://101.96.128.94:9999/mfresh/'+item.pic" alt />
              </a>
              <a class="cart_title">{{item.title1}}</a>
              <i>¥{{item.price}}</i>
              <div>
                <span @click="changecount('-',index)">-</span>
                <input type="text" v-model="item.count" @input='countchange(index)'/>
                <span @click="changecount('+',index)">+</span>
              </div>
              <strong>¥{{item.price*item.count}}</strong>
              <em @click="delpro(item.did,index)"></em>
            </li>
          </ul>
        </div>
        <div class="cart_header">
          <span>
            已选商品
            <em>{{count}}</em>件
          </span>
          <span>
            总金额：
            <strong>￥{{total}}</strong>
          </span>
          <button type="button">结算</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  mounted() {
    let url = "cart_detail_select.php";
    let params = "uid=" + this.$store.state.uid;
    this.axios
      .post(url, params)
      .then(res => {
        this.pro = res.data.products;
        // 人为给每条数据增加一条属性来代表是否被勾选
        this.pro.forEach((elem,i)=>{
          elem.checked=false;// 默认都是未选中
        })
      })
      .catch(err => {
        console.error(err);
      });
  },
  data() {
    return {
      pro: [],
      total:0,
      count:0,// 件数
      allin:false, // 全选按钮值 
    };
  },
  methods: {
    delpro(did,index) {
      let url = "cart_detail_delete.php?did=" + did;
      this.axios
        .get(url)
        .then(res => {
          // this.$router.go(0)
          if(res.data.code){
            // 删除对应的项目
            this.pro.splice(index,1)
          }
        })
        .catch(err => {
          console.error(err);
        });
    },
    docheck(){
      this.count=0;
      let total=0
      this.pro.forEach(i=>{
        if(i.checked){
          total+=i.price*i.count
          this.count+=1
        }
      })
      this.total=total
    },
    changecount(a,index){
      if(a=="+"){
        this.pro[index].count++
      }else if(a=="-"){
        if(this.pro[index].count<=1){
          alert("商品最少为1")
          this.pro[index].count=1
        }else{
          this.pro[index].count-=1
        }
      }
      this.countchange(index);
    },
    countchange(index){
      if(this.pro[index].count<1){
        alert("商品最少为1")
          this.pro[index].count=1
      }
      let url="cart_detail_update.php"
      let params="did="+this.pro[index].did+"&pid="+this.pro[index].pid+'&count='+this.pro[index].count
      this.axios.post(url,params)
      .then(res => {
        console.log(res)
      })
      .catch(err => {
        console.error(err); 
      })
    }
  },
  // 计算属性 属性的值是通过一个方法获得的，方法中的任何元素变化时属性的值都会自动变化
  computed:{

  },
  watch:{
    count(newvalue){
      if(newvalue==this.pro.length){
        this.allin=true
      }
    },
    allin(newvalue){
      // 监听 全选框的值
      // 让所有的选择框都和此框的值相同
      this.pro.forEach(e=>{
        e.checked=newvalue
        this.docheck()
      })
    },

  }
};
</script>

