<template>
  <div>
    <div class="breadcrumb">
      <div class="container">
        <h2>
          <router-link to="/">首页</router-link>&gt;
          <a href>产品中心</a>
        </h2>
      </div>
    </div>
    <!--页面主体-->
    <div class="main container">
      <div class="pl_header">
        <a @click="changeType(1)" :class="{cur:type==1}">净美仕净化器</a>
        <a @click="changeType(2)" :class="{cur:type==2}">净美仕滤网</a>
      </div>
      <!-- 产品列表-->
      <ul class="product_list clearfloat">
        <li v-for="(p,i) in pro" :key="i">
          <router-link :to="'/productdetails/'+p.pid">
            <img :src="p.pic" alt />
          </router-link>
          <div class="pdlist_text clearfloat">
            <h3>
              <p>{{p.model}}</p>
              <span>{{p.title2}}</span>
            </h3>
            <router-link :to="{name:'ProductDetails',params:{pid:p.pid}}">查看详情</router-link>
          </div>
        </li>
      </ul>
      <!-- 分页导航-->
      <div class="pages">
        <a @click="pageNum>1 && getData(pageNum-1)" :class="{default:pageNum==1}">上一页</a>
        <a @click="getData(e)" v-for="(e,i) of pageCount" :key="i" :class="{cur:pageNum==e}">{{e}}</a>
        <a
          @click="pageNum<pageCount && getData(pageNum+1)"
          :class="{default:pageNum==pageCount}"
        >下一页</a>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  // 只有data中的值可以直接在html中使用
  data() {
    return {
      // 只在组件实例化时会执行一次
      type: this.$route.params.type,
      pro: [],
      pageNum: 1,
      pageCount: 0
    };
  },
  methods: {
    getData(pno) {
      let url = "product_select.php?type=" + this.type + "&pageNum=" + pno;
      this.axios
        .get(url)
        .then(res => {
          this.pro = res.data.data;
          if (this.pro) {
            this.pro.map((elem, i) => {
              return (elem.pic =
                "http://101.96.128.94:9999/mfresh/" + elem.pic);
            });
          }
          this.pageNum = res.data.pageNum;
          this.pageCount = res.data.pageCount;
        })
        .catch(err => {
          console.error(err);
        });
    },
    changeType(n) {
      this.type = n;
      this.getData(1);
    }
  },
  mounted() {
    this.getData(1);
  },
  // 监听 指定某个元素，该元素变化时会触发监听
  watch: {
    // 监听地址栏的变化
    $route(newvalue) {
      this.type = newvalue.params.type;
      this.getData(1);
    }
  }
};
</script>

