<template>
  <div>
    <div class="breadcrumb">
      <div class="container">
        <h2>
          <router-link to="/">首页</router-link>&gt;
          <a href>公司动态</a>
        </h2>
      </div>
    </div>
    <!--页面主体-->
    <div class="main container">
      <div class="news">
        <ul v-show="!!news.length">
          <li v-for="(n,i) in news" :key="i">
            <span>{{n.pubTime | date()}}</span>
            <!-- <router-link :to="'details/'+n.nid" >{{n.title}}</router-link> -->
            <router-link :to="{name:'Details',params:{nid:n.nid}}">{{n.title}}</router-link>
          </li>
        </ul>
      </div>
      <!-- 分页导航-->
      <!-- a标签要去掉href，或者添加@click.prevent 否则会触发a标签的原生跳转操作 -->
      <div class="pages" v-show="!!pageCount">
        <a @click="pageNum>1&& getNews(pageNum-1)" :class="{default:pageNum==1}">上一页</a>
        <a v-for="(e,i) of pageCount" :key="i" :class="{cur:e==pageNum}" @click="getNews(e)">{{e}}</a>
        <a href="javascript:;" @click.prevent="pageNum<pageCount&&getNews(pageNum+1)" :class="{default:pageNum==pageCount}">下一页</a>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data(){
    return{
      news:[],pageNum:1,pageSize:0,pageCount:0,totalRecord:0
    }
  },
  mounted(){
    this.getNews(1)
  },
  methods:{
    getNews(pno){
      let url="news_select.php";
      let params="pageNum="+pno
      this.axios.post(url,params)
      .then(res => {
        this.news=res.data.data;
        this.pageNum=res.data.pageNum;
        this.pageSize=res.data.pageSize;
        this.pageCount=res.data.pageCount;
        this.totalRecord=res.data.totalRecord;
      })
      .catch(err => {
        console.error(err); 
      })
    }
  },
  filters:{
    date(d){
      let ts=parseInt(d)
      let date=new Date(ts);
      let year=date.getFullYear();
      let month=date.getMonth()+1;
      let day=date.getDay()
      return `${year}-${month}-${day}`
    }
  }
};
</script>

